.services-card {
  padding: 38px 42px;
  background-color: #161616;
  border-bottom: 2px solid transparent;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  padding: 38px 42px;
  transition: all 0.15s ease-in-out;
}
.services-card:hover {
  border-bottom-color: var(--secondary-color);
}
.card-heading {
  font-size: 20px;
  margin-bottom: 14px;
  font-weight: 500;
}
.card-icon {
  left: 0;
  margin-bottom: 20px;
}
