.contact-divider {
  width: 100px;
  height: 4px;
}
.about-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding-top: 50px;
  position: relative;
}
.about-title {
  line-height: 1.6;
  margin-bottom: 16px;
  font-size: 1.5rem;
}
.about-content {
}
.about-info {
}
.about-spacer {
  width: 100px;
  height: 2px;
  margin: 0 20px;
}
.resume-card {
  background-color: #161616;
  border-left: 2px solid transparent;
  padding: 32px 48px;
  position: relative;
}
.resume-card {
  border-left-color: var(--secondary-color);
}
.resume-card:not(:last-child) {
  border-bottom: 1px solid #313131;
}
.item-arrow::before {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  height: 16px;
  width: 16px;
  background-color: var(--secondary-color);
}
.item-arrow::after {
  border: 8px solid transparent;
  left: 16px;
  position: absolute;
  display: block;
  border-left-color: var(--secondary-color);
}
.cta-btn {
  max-width: 300px;
}
@media only screen and (max-width: 768px) {
  .about-img {
    border-radius: 50%;
    height: 200px;
    width: 200px;
  }
  .download-btn {
    transform: scale(0.5);
  }
}
