/* ++++++++++++++++++++++++++++++++++++++  FONTS IMPORT ++++++++++++++++++++++++++ */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bungee+Outline&display=swap');

/* Defaults */
:root {
  --primary-color: #111;
  --secondary-color: #009e66;
  --light: #9f9f9f;
  --dark: #333;
  --font-lg: 2.625rem;
  --font-md: 1.625rem;
  --font-sm: 0.875rem;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #111 !important;
  color: #9f9f9f !important;
  font-family: 'Poppins', sans-serif !important;
  font-size: 14px !important;
  font-weight: 400 !important;
  overflow-x: hidden;
  position: relative !important;
}
.text-primary {
  color: var(--secondary-color) !important;
}
.btn-primary {
  background-color: var(--secondary-color);
  border: none;
}

.section-header {
}
.section-title {
}
.section-subtitle {
}
.content-title {
  font-size: 31px;
  font-weight: 700;
  margin-bottom: 16px;
  line-height: 1.6;
}

.content-subtitle {
  font-size: 24px;
  margin-bottom: 15px;
}
.content-description {
  border-bottom: 2px solid rgba(255, 255, 255, 0.08);
  margin-bottom: 0;
  padding-bottom: 29px;
  font-size: 15px;
  line-height: 1.95;
}
/* Utility Classes */
.container {
  width: 80%;
  margin: auto;
}
/* Buttons */
.btn-primary {
  background-color: var(--secondary-color) !important;
  color: #fff;
}
.bg-primary {
  background-color: var(--secondary-color) !important;
  color: #fff;
}

a {
  color: inherit;
}

/* Social */

.social {
  transition: all 0.4s ease-in-out;
}
.social:hover {
  color: var(--secondary-color);
  transform: scale(1.5);
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .contact-icons {
    transform: scale(0.6);
  }
}
