.nav-container {
  width: 100%;
  position: fixed;
  z-index: 1000;
  background-color: var(--primary-color);
}
nav {
  background-color: transparent;
}
.brand {
  z-index: 1000;
}
nav ul {
  list-style-type: none;
  display: flex;
}
nav ul li {
  margin-left: 1.5rem;
}
nav ul li:last-child {
  left: 0;
}
nav ul li:hover {
  border-bottom: 1px solid var(--secondary-color);
}
nav ul li a {
  text-decoration: none;
  color: var(--light);
  transition: all 0.5s ease-in-out;
}
nav ul li a:hover {
  color: #fff;
}
.app-bar {
  display: none;
}
.mobile-nav {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: var(--primary-color);
  z-index: 200;
}
.mobile-nav-container {
  max-width: 100%;
  padding: 0 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100vh;
  margin: 2rem auto;
  position: relative;
}

.mobile-nav-navlinks ul {
  list-style-type: none;
}
.mobile-nav-navlinks ul li {
  margin-bottom: 0.84rem;
}
.mobile-nav-navlinks ul li a {
  font-size: 1.5rem;
  color: var(--light);
  text-decoration: none;
  transition: 0.5s;
}
.mobile-nav-navlinks ul li a:hover {
  color: var(--secondary-color);
}
.mobile-social {
  width: 150px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 60px;
}
.mobile-nav-close {
  position: absolute;
  z-index: 322;
  top: 0;
  right: 30px;
}

@media only screen and (max-width: 768px) {
  nav ul {
    display: none;
  }
  .app-bar {
    display: block;
    z-index: 1000;
  }
  .mobile-nav-container {
    display: flex;
  }
}
