@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee+Outline&display=swap);
/* ++++++++++++++++++++++++++++++++++++++  FONTS IMPORT ++++++++++++++++++++++++++ */

/* Defaults */
:root {
  --primary-color: #111;
  --secondary-color: #009e66;
  --light: #9f9f9f;
  --dark: #333;
  --font-lg: 2.625rem;
  --font-md: 1.625rem;
  --font-sm: 0.875rem;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #111 !important;
  color: #9f9f9f !important;
  font-family: 'Poppins', sans-serif !important;
  font-size: 14px !important;
  font-weight: 400 !important;
  overflow-x: hidden;
  position: relative !important;
}
.text-primary {
  color: #009e66 !important;
  color: var(--secondary-color) !important;
}
.btn-primary {
  background-color: #009e66;
  background-color: var(--secondary-color);
  border: none;
}

.section-header {
}
.section-title {
}
.section-subtitle {
}
.content-title {
  font-size: 31px;
  font-weight: 700;
  margin-bottom: 16px;
  line-height: 1.6;
}

.content-subtitle {
  font-size: 24px;
  margin-bottom: 15px;
}
.content-description {
  border-bottom: 2px solid rgba(255, 255, 255, 0.08);
  margin-bottom: 0;
  padding-bottom: 29px;
  font-size: 15px;
  line-height: 1.95;
}
/* Utility Classes */
.container {
  width: 80%;
  margin: auto;
}
/* Buttons */
.btn-primary {
  background-color: #009e66 !important;
  background-color: var(--secondary-color) !important;
  color: #fff;
}
.bg-primary {
  background-color: #009e66 !important;
  background-color: var(--secondary-color) !important;
  color: #fff;
}

a {
  color: inherit;
}

/* Social */

.social {
  transition: all 0.4s ease-in-out;
}
.social:hover {
  color: #009e66;
  color: var(--secondary-color);
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  cursor: pointer;
}
@media only screen and (max-width: 768px) {
  .contact-icons {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
  }
}

.services-card {
  padding: 38px 42px;
  background-color: #161616;
  border-bottom: 2px solid transparent;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  padding: 38px 42px;
  transition: all 0.15s ease-in-out;
}
.services-card:hover {
  border-bottom-color: var(--secondary-color);
}
.card-heading {
  font-size: 20px;
  margin-bottom: 14px;
  font-weight: 500;
}
.card-icon {
  left: 0;
  margin-bottom: 20px;
}

.contact-divider {
  width: 100px;
  height: 4px;
}
.about-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding-top: 50px;
  position: relative;
}
.about-title {
  line-height: 1.6;
  margin-bottom: 16px;
  font-size: 1.5rem;
}
.about-content {
}
.about-info {
}
.about-spacer {
  width: 100px;
  height: 2px;
  margin: 0 20px;
}
.resume-card {
  background-color: #161616;
  border-left: 2px solid transparent;
  padding: 32px 48px;
  position: relative;
}
.resume-card {
  border-left-color: var(--secondary-color);
}
.resume-card:not(:last-child) {
  border-bottom: 1px solid #313131;
}
.item-arrow::before {
  content: "";
  position: absolute;
  left: 0;
  display: block;
  height: 16px;
  width: 16px;
  background-color: var(--secondary-color);
}
.item-arrow::after {
  border: 8px solid transparent;
  left: 16px;
  position: absolute;
  display: block;
  border-left-color: var(--secondary-color);
}
.cta-btn {
  max-width: 300px;
}
@media only screen and (max-width: 768px) {
  .about-img {
    border-radius: 50%;
    height: 200px;
    width: 200px;
  }
  .download-btn {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

.port-container {
  padding-top: 100px;
  width: 100%;
  height: 100%;
}

.tab-header {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}
.tab-item {
  margin-right: 10px;
  font-size: 18px;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
/* .tab-item:first-child {
  margin-right: 0px;
} */
.tab-item:hover,
.tab-item.active {
  border-bottom: 2px solid var(--secondary-color);
  color: var(--secondary-color);
}
/* .port-container .contact-header {
  background-color: green;
} */
.project-card {
  height: 400px;
  /* border: 2px solid var(--secondary-color); */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.project-card:hover .port-card-content {
  opacity: 1;
  visibility: visible;
  border-bottom: 2px solid var(--secondary-color);
}
.project-card img {
  max-width: 100%;
  height: auto;
}
.port-card-content {
  left: 0;
  opacity: 0;
  padding: 3px 18px;
  position: absolute;
  text-align: center;
  transition: all 0.15s ease-in-out;
  visibility: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
  background-color: rgba(8, 0, 0, 0.842);
}

.port-card-content a {
  color: var(--secondary-color);
  text-decoration: none;
  transition: all 0.5s ease-in-out;
  font-size: 18px;
}
.port-card-content a:hover {
  text-decoration: underline;
}
.port-container .contact-header p {
  text-align: center;
}

.smiley {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primary-color);
  position: absolute;
  top: 0;
  left: 0;
}

.nav-container {
  width: 100%;
  position: fixed;
  z-index: 1000;
  background-color: var(--primary-color);
}
nav {
  background-color: transparent;
}
.brand {
  z-index: 1000;
}
nav ul {
  list-style-type: none;
  display: flex;
}
nav ul li {
  margin-left: 1.5rem;
}
nav ul li:last-child {
  left: 0;
}
nav ul li:hover {
  border-bottom: 1px solid var(--secondary-color);
}
nav ul li a {
  text-decoration: none;
  color: var(--light);
  transition: all 0.5s ease-in-out;
}
nav ul li a:hover {
  color: #fff;
}
.app-bar {
  display: none;
}
.mobile-nav {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: var(--primary-color);
  z-index: 200;
}
.mobile-nav-container {
  max-width: 100%;
  padding: 0 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  height: 100vh;
  margin: 2rem auto;
  position: relative;
}

.mobile-nav-navlinks ul {
  list-style-type: none;
}
.mobile-nav-navlinks ul li {
  margin-bottom: 0.84rem;
}
.mobile-nav-navlinks ul li a {
  font-size: 1.5rem;
  color: var(--light);
  text-decoration: none;
  transition: 0.5s;
}
.mobile-nav-navlinks ul li a:hover {
  color: var(--secondary-color);
}
.mobile-social {
  width: 150px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 60px;
}
.mobile-nav-close {
  position: absolute;
  z-index: 322;
  top: 0;
  right: 30px;
}

@media only screen and (max-width: 768px) {
  nav ul {
    display: none;
  }
  .app-bar {
    display: block;
    z-index: 1000;
  }
  .mobile-nav-container {
    display: flex;
  }
}

