.port-container {
  padding-top: 100px;
  width: 100%;
  height: 100%;
}

.tab-header {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}
.tab-item {
  margin-right: 10px;
  font-size: 18px;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}
/* .tab-item:first-child {
  margin-right: 0px;
} */
.tab-item:hover,
.tab-item.active {
  border-bottom: 2px solid var(--secondary-color);
  color: var(--secondary-color);
}
/* .port-container .contact-header {
  background-color: green;
} */
.project-card {
  height: 400px;
  /* border: 2px solid var(--secondary-color); */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  position: relative;
  overflow: hidden;
}
.project-card:hover .port-card-content {
  opacity: 1;
  visibility: visible;
  border-bottom: 2px solid var(--secondary-color);
}
.project-card img {
  max-width: 100%;
  height: auto;
}
.port-card-content {
  left: 0;
  opacity: 0;
  padding: 3px 18px;
  position: absolute;
  text-align: center;
  transition: all 0.15s ease-in-out;
  visibility: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
  background-color: rgba(8, 0, 0, 0.842);
}

.port-card-content a {
  color: var(--secondary-color);
  text-decoration: none;
  transition: all 0.5s ease-in-out;
  font-size: 18px;
}
.port-card-content a:hover {
  text-decoration: underline;
}
.port-container .contact-header p {
  text-align: center;
}
